
import { useStore } from "vuex";
import { key, MutationTypes } from "@/store";
export default {
  name: "Dialog",
  // eslint-disable-next-line
  setup() {
    const store = useStore(key);
    const onclick = () => store.commit(MutationTypes.OpenDialog, "none");
    return {
      onclick,
    };
  },
};
