
export default {
  name: "Status",
  emits: ["onclick"],
  props: {
    title: String,
    content: String,
    clickable: Boolean,
  },
  // eslint-disable-next-line
  setup(_: unknown, { emit }: { emit: any }) {
    const onclick = (e: Event) => emit("onclick", e);
    return {
      onclick,
    };
  },
};
